import { useEffect, useRef } from 'react'

const useReloadOnOrentation = () => {
  const prevOri = useRef()
  useEffect(() => {
    if (typeof window !== 'undefined') {
      window.addEventListener("orientationchange", (event) => {
        if (typeof prevOri.current === 'number') {
          if (prevOri.current !== event.target.screen.orientation.angle) {
            setTimeout(() => window.location.reload())
          }
        }
        prevOri.current = event.target.screen.orientation.angle
      });
    }
  }, [])
}

export default useReloadOnOrentation
